import { get, writable } from 'svelte/store';
import EditorJS from '@editorjs/editorjs';
// Initial state for the language data
export const Language = writable({
    Version: '2.0.0',
    Name: 'Unnamed Language',
    CaseSensitive: false,
    IgnoreDiacritics: true,
    HeaderTags: '',
    Lexicon: {},
    Alphabet: 'a b c d e f g h i j k l m n o p q r s t u v w x y z',
    Pronunciations: {
        General: 'th > '
    },
    Phonotactics: {
        General: {
            Onsets: [],
            Medials: [],
            Codas: [],
            Vowels: [],
            Illegals: ['']
        }
    },
    Lects: ['General'],
    Phrasebook: {},
    Docs: {
        blocks: []
    },
    Diagnostics: []
});
export const wordInput = writable('');
export const pronunciations = writable((() => {
    const inputs = {};
    for (const lect of get(Language).Lects) {
        inputs[lect] = '';
    }
    return inputs;
})());
export const phraseInput = writable('');
export const phrasePronunciations = writable((() => {
    const inputs = {};
    for (const lect of get(Language).Lects) {
        inputs[lect] = '';
    }
    return inputs;
})());
export const categoryInput = writable('');
export const selectedCategory = writable('');
export const pronunciationRules = writable({ General: { th: '' } });
export const docsEditor = writable(new EditorJS);
export const theme = writable('styles/dark.css');
export const autosave = writable(true);
export const useDialects = writable(false);
get(Language).Phonotactics;
